<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <van-address-edit
      :address-info="addressinfo"
      :area-list="areaList"
      show-postal
      show-delete
      show-set-default
      show-search-result
      :search-result="searchResult"
      :area-columns-placeholder="['', '', '']"
      @save="onSave"
      @delete="onDelete"
      @change-detail="onChangeDetail"
    />
   
  </div>
</template>

<script>
import areaList from "../assets/area.js";
export default {
  data() {
    return {
      active: 2,
      areaList,
      searchResult: [],
      addressinfo:{
        "name":""
      }
    };
  },
  mounted(){
    let data = this.$route.query
    this.addressinfo=data
  },
  methods: {
    onClickLeft() {
      this.$router.push("/location");
    },
    onSave() {
      let that=this;
      that.$toast.success("");
      window.console.log(that.addressinfo)
      setTimeout(function(){
        that.$router.push("/location");
      },1000)
    },
    onDelete() {
      this.$toast.success("");
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "",
            address: ""
          }
        ];
      } else {
        this.searchResult = [];
      }
    }
  }
};
</script>

