<template>
  <div class="commodity_details">
    <div class="nav_bar_h"></div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />

    <van-image-preview v-model="show" :images="images" @change="onChange" :startPosition="index">
      <!-- <template v-slot:index>{{ index }}</template> -->
    </van-image-preview>

    <!--  -->
    <van-swipe :autoplay="3000" class="home_lb">
      <van-swipe-item v-for="(image, index) in images" :key="index" @click="img_click(index)">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>

    <div class="details_title">
      <div class="details_h">Apple/iPhone 8 128G  </div>
      <div class="details_title_s">
        <div class="details_title_p">
          {{ imputed_price_s(imputed_price) }}
          <span :hidden="time_time">999</span>
        </div>
        <div class="details_title_time" :hidden="time_time">
          <span></span>
          <van-count-down :time="time" @finish="finish">
            <template v-slot="timeData">
              <span class="item">{{ timeData_s(timeData.hours) }}</span>
              <span class="item">{{ timeData_s(timeData.minutes) }}</span>
              <span class="item">{{ timeData_s(timeData.seconds) }}</span>
            </template>
          </van-count-down>
        </div>
      </div>
    </div>
    <!--  -->
    <van-coupon-cell :coupons="coupons" :chosen-coupon="chosenCoupon" @click="showList = true" />

    <!--  -->
    <van-popup v-model="showList" position="bottom" style="text-align: left;">
      <van-coupon-list
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        :disabled-coupons="disabledCoupons"
        @change="onChange_yhj"
        @exchange="onExchange_yhj"
      />
    </van-popup>

    <van-cell title="" is-link :value="item_list" @click="Choose_specifications" />

    <!--  -->
    <van-divider :style="{ color: '#333', borderColor: '#333', padding: '0 16px' }"></van-divider>

    <div class="imageList_s">
      <img v-for="img in imageList_s" v-lazy="img" />
    </div>

    <!--  -->
    <div class="specification">
      <van-sku
        v-model="show_specification"
        :sku="sku"
        :goods="goods"
        :goods-id="goodsId"
        :initial-sku="initialSku"
        @buy-clicked="onBuyClicked"
        @add-cart="onAddCartClicked"
        @sku-selected="selected_sku"
        @stepper-change="stepper_change"
        ref="getSku"
      />
    </div>

    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="" />
      <van-goods-action-icon icon="cart-o" text="" :info="cart_info" @click="router('/cart')" />
      <!-- <van-goods-action-icon icon="shop-o" text="" info="12" /> -->
      <van-goods-action-button type="warning" text="" @click="onAddCartClicked" />
      <van-goods-action-button type="danger" text="" @click="onBuyClicked" />
    </van-goods-action>
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "\n12",
  reason: "",
  value: 150,
  name: "",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "1.5",
  unitDesc: ""
};
export default {
  data() {
    return {
      time_time:false,
      time: 100000,
      timeData: {},
      loading: true,
      show: false,
      index: 0,
      images: [
        "https://img.yzcdn.cn/vant/apple-1.jpg",
        "https://img.yzcdn.cn/vant/apple-2.jpg",
        "https://img.yzcdn.cn/vant/apple-3.jpg",
        "https://img.yzcdn.cn/vant/apple-4.jpg"
      ],

      imageList_s: [
        "https://img.yzcdn.cn/vant/apple-1.jpg",
        "https://img.yzcdn.cn/vant/apple-2.jpg",
        "https://img.yzcdn.cn/vant/apple-3.jpg",
        "https://img.yzcdn.cn/vant/apple-4.jpg",
        "https://img.yzcdn.cn/vant/apple-5.jpg",
        "https://img.yzcdn.cn/vant/apple-6.jpg",
        "https://img.yzcdn.cn/vant/apple-7.jpg",
        "https://img.yzcdn.cn/vant/apple-8.jpg"
      ],
      // id
      //
      show_specification: false,
      goodsId: "666",
      sku: {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: "1", // skuValueId id
                name: "", // skuValueName
                imgUrl: "https://img.yzcdn.cn/vant/apple-1.jpg", // 
                previewImgUrl: "https://img.yzcdn.cn/vant/apple-1.jpg" // 
              },
              {
                id: "2",
                name: "",
                imgUrl: "https://img.yzcdn.cn/vant/apple-3.jpg",
                previewImgUrl: "https://img.yzcdn.cn/vant/apple-3.jpg"
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          },
          {
            k: "", // skuKeyName
            v: [
              {
                id: "3", // skuValueId id
                name: "64G" // skuValueName
              },
              {
                id: "4",
                name: "128G"
              }
            ],
            k_s: "s2" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: 1111, // skuId
            price: 10000, // 
            s1: "1", //  k_s  s1  id
            s2: "3", //  k_s  s2  id
            stock_num: 10 //  sku 
          },
          {
            id: 2222, // skuId
            price: 19999, // 
            s1: "1", //  k_s  s1  id
            s2: "4", //  k_s  s2  id
            stock_num: 10 //  sku 
          },
          {
            id: 3333, // skuId
            price: 20000, // 
            s1: "2", //  k_s  s1  id
            s2: "3", //  k_s  s2  id
            stock_num: 10 //  sku 
          },
          {
            id: 4444, // skuId
            price: 29999, // 
            s1: "2", //  k_s  s1  id
            s2: "4", //  k_s  s2  id
            stock_num: 10 //  sku 
          }
        ],
        price: "10000", // 
        stock_num: 10, // 
        collection_id: 2259, //  skuId  collection_id sku  id
        none_sku: false // 
      },
      goods: {
        // 
        title: "",
        //  sku 
        picture: "https://img.yzcdn.cn/vant/apple-2.jpg"
      },
      //
      initialSku: {
        s1: "1",
        s2: "3",
        // 
        selectedNum: 1
      },
      cart_info: 3,
      // ================================
      showList: false,
      chosenCoupon: -1,
      //
      coupons: [
        {
          id: 1,
          type: "1",
          condition: "\n12",
          value: 150,
          name: "",
          startAt: 1489104000,
          endAt: 1514592000,
          valueDesc: "1.5",
          unitDesc: "",
          description: ""
        },
        {
          id: 2,
          type: "2",
          condition: "\n12",
          value: 1550,
          name: "",
          startAt: 1489104000,
          endAt: 1514592000,
          valueDesc: "8.8",
          unitDesc: "",
          description: ""
        }
      ],
      //
      disabledCoupons: [
        {
          id: 3,
          type: "2",
          condition: "\n12",
          value: 1550,
          name: "",
          startAt: 1489104000,
          endAt: 1514592000,
          valueDesc: "15.5",
          unitDesc: "",
          reason: ""
        }
      ],
      skuValue_list: [],
      item_list: "",
      imputed_price:"9999",
      valueDesc_price:"0"
    };
  },
  mounted() {
    let that = this;
    let id = that.$route.params.id;
    that.goodsId = id;
    that.getSkuData_s();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //
    onChange(index) {
      this.index = index;
    },
    //
    img_click(index) {
      console.log(index);
      this.index = index;
      this.show = true;
    },
    // 
    onBuyClicked() {
      let that = this;
      if (that.item_list == "") {
        that.show_specification = true;
        return false;
      }
      let getSkuData_list = that.$refs.getSku.getSkuData();
      console.log(getSkuData_list);
    },
    // 
    onAddCartClicked(e) {
      let that = this;
      if (that.item_list == "") {
        that.show_specification = true;
        return false;
      }
      let getSkuData_list = that.$refs.getSku.getSkuData();
      that.show_specification = false;
      that.cart_info += 1;
      console.log(getSkuData_list);
    },
    //
    onChange_yhj(index) {
      let that=this
      that.showList = false;
      console.log(index);
      if (index != -1) {
        that.chosenCoupon = index;
        let id = that.coupons[index].id;
        let type = that.coupons[index].type;
        that.valueDesc_price=that.coupons[index].value;
        let getSkuData_list = that.$refs.getSku.getSkuData();
        that.imputed_price=getSkuData_list.selectedSkuComb.price-that.valueDesc_price;
        console.log("yhj_id", that.coupons[index]);
      }else{
        that.chosenCoupon = -1;
        that.valueDesc_price=0;
        let getSkuData_list = that.$refs.getSku.getSkuData();
        that.imputed_price=getSkuData_list.selectedSkuComb.price-that.valueDesc_price;
      }
    },
    //
    onExchange_yhj(code) {
      this.coupons.push(coupon);
    },
    selected_sku(e) {
      let that = this;
      that.getSkuData_s();
    },
    stepper_change(e) {
      let that = this;
      console.log(e);
      that.getSkuData_s(e);
    },
    Choose_specifications() {
      this.show_specification = true;
    },
    getSkuData_s(val) {
      let that = this;
      let item1;
      let item2;
      let item3;
      let item_list;
      let item_quantity;
      let getSkuData_list = that.$refs.getSku.getSkuData();
      if (val) {
        item_quantity = val;
      } else {
        item_quantity = getSkuData_list.selectedNum;
      }
      console.log(getSkuData_list);
      if (getSkuData_list.selectedSkuComb) {
        if (getSkuData_list.selectedSkuComb.s1) {
          let items = that.sku.tree[0].v.find(item => {
            return item.id == getSkuData_list.selectedSkuComb.s1;
          });
          item1 = items.name;
        }
        if (getSkuData_list.selectedSkuComb.s2) {
          let items = that.sku.tree[1].v.find(item => {
            return item.id == getSkuData_list.selectedSkuComb.s2;
          });
          item2 = items.name;
        }
        if (getSkuData_list.selectedSkuComb.s3) {
          let items = that.sku.tree[2].v.find(item => {
            return item.id == getSkuData_list.selectedSkuComb.s3;
          });
          item3 = items.name;
        }
        if (item1) {
          item_list = item1 + "," + item_quantity;
        }
        if (item2) {
          item_list = item1 + "," + item2 + "," + item_quantity;
        }
        if (item3) {
          item_list =
            item1 + "," + item2 + "," + item3 + "," + item_quantity;
        }

        that.imputed_price=getSkuData_list.selectedSkuComb.price-that.valueDesc_price;
        console.log(item_list);
        that.item_list = item_list;
      }
    },
    router(val) {
      window.console.log(val);
      this.$router.push(val).catch(err => {
        console.log(err);
      });
    },
    timeData_s(val) {
      let times = val;
      if (times <= 9) {
        times = "0" + times;
      }
      return times;
    },
    imputed_price_s(val){
      let price_s=""+val/100
      return price_s
    },
    finish() {
      this.$toast('');
      this.time_time=true;
    }
  }
};
</script>

<style scoped>
.home_lb img {
  width: 7.5rem;
  height: 7.5rem;
  float: left;
}
.specification {
  text-align: left;
}
.commodity_details {
  background: #eee;
}
.imageList_s {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.imageList_s img {
  width: 100%;
}
.item {
  display: inline-block;
  width: 22px;
  margin-right: 5px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #000;
}
.details_title {
  padding: 0 0.2rem 0.2rem;
  background: #ff6034;
  padding-top: 0.1rem;
}
.details_h {
  color: #fff;
  font-weight: 600;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  text-align: left;
}
.details_title_s {
  width: 100%;
  margin-top: 0.1rem;
}
.details_title_s:after {
	content: "200b";
	display: block;
	font-size: 0;
	height: 0;
	clear: both
}
.details_title_p {
  float: left;
  color: #fff;
  font-size: 0.4rem;
  line-height: 0.8rem;
}
.details_title_p span {
  font-size: 0.3rem;
  text-decoration: line-through;
  color: #eee;
}
.details_title_time {
  float: right;
}
.details_title_time span {
  font-size: 0.28rem;
  color: #fff;
}
</style>>
